import { FormManager } from '../Common/FormManager';
import { onDocReady } from '../Common/UiHelper';

export class RegisterRoomManager {

    async Init(): Promise<void> {
        let backButton = document.getElementById("registerRoomBackSubmitButton") as HTMLButtonElement;
        if (backButton !== null) {
            backButton.addEventListener("click", (e: Event) => this.Save(1));
        }

        let nextButton = document.getElementById("registerRoomNextSubmitButton") as HTMLButtonElement;
        if (nextButton !== null) {
            nextButton.addEventListener("click", (e: Event) => this.Save(3));
        }

        let step1Tab = document.getElementById("step1Tab") as HTMLLIElement;
        if (step1Tab !== null) {
            step1Tab.addEventListener("click", (e: Event) => this.Save(1));
        }

        let step3Tab = document.getElementById("step3Tab") as HTMLLIElement;
        if (step3Tab !== null) {
            step3Tab.addEventListener("click", (e: Event) => this.Save(3));
        }
    }

    async Save(step:number): Promise<void> {
        let formManager = new FormManager('roomEditorForm');
        if (await formManager.ValidateForm()) {
            let response = await formManager.SubmitForm();
            if (response.ok) {
                let host = document.getElementById("HostName") as HTMLInputElement;
                if (host != null) {
                    let data = JSON.parse(response.data)
                    window.location.href = `${host.value}/meeting-space/register/${step}/${data.SpaceId}/${data.RoomId}`;
                } else {
                    alert('Host Name is not configured');
                }
            } else {
                await formManager.ShowSuccessFailureMessage(response);
            }
        }
    }

}
onDocReady(async () => {
    let page = new RegisterRoomManager();
    await page.Init();
});